body.lock {
	overflow-y: hidden;
}

.dashboard {
	background: #bbb;
}

header {
	position: fixed;
	top: 0;
	width: 100%;
	left: 0;
	z-index: 2;
	background: #bbb;
}

.negate-all {
	background: #f00;
	color: #fff;
	border: none;
	padding: 5px 10px;
	font-size: 18px;
	font-weight: bold;
	margin: 10px;
	cursor: pointer;
}

.counter {
	float: right;
	background: #2b3b82;
	color: #fff;
	font-size: 18px;
	padding: 7px 10px;
	letter-spacing: 1px;
	border-radius: 5px;
	z-index: 2;
	margin: 10px;
}

.removerLikes, .removerDislikes {
	display: inline-block;
	margin-top: 17px;
	vertical-align: top;
}

.removerLikes input, .removerDislikes input {
	margin-right: 10px;
}

.removerDislikes {
	margin-left: 20px;
	user-select: none;
}

.list {
	display: flex;
	width: 100%;
	min-height: 100%;
	flex-wrap: wrap;
	padding-top: 50px;
	justify-content: center;
	overflow-x: hidden;
}
