.profile {
  width: 25%;
  max-width: 380px;
  background: #000;
  margin: 20px;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.profile img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}

.profile .info {
  position: absolute;
  width: 100%;
  bottom: 0;
  background-image: linear-gradient(transparent, #000000);
  padding: 20px 0;
}

.profile .info .name {
  display: inline-block;
  color: #ffffff;
  font-size: 25px;
  padding: 10px 20px;
  width: 100%;
  text-shadow: 0 0 10px #000;
  margin-bottom: 15px;
}

.profile .info .actions {
  display: flex;
  width: 100%;
  justify-content: center;
}

.profile .info .actions button {
  background: transparent;
  width: 60px;
  height: 60px;
  border-radius: 30px;
  border: solid 3px currentColor;
  font-size: 23px;
  margin: 0 10px;
  cursor: pointer;
}

.profile .info .actions button.hidden {
  display: none;
}

.profile .info .actions button[disabled] {
  cursor: default;
}

.profile .info .actions button.dislike {
  color: #fe4a69;
}

.profile .info .actions button.like {
  color: #22eabc;
}

.full-profile {
  position: fixed;
  background: rgba(0, 0, 0, .7);
  width: 100%;
  height: 100%;
  z-index: 3;
  top: 0;
  display: flex;
  flex-wrap: wrap;
}

.full-profile .preview {
  display: flex;
  flex-grow: 1;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.full-profile .preview img {
  max-width: 90%;
  max-height: 90%;
  object-fit: contain;
}

.full-profile .info {
  width: 500px;
  background: #fff;
  display: flex;
  flex-direction: column;
  position: relative;
  height: 100%;
}

.full-profile .info h1 {
  display: inline-block;
  margin: 0;
  padding: 15px;
}

.full-profile .info .distance {
  display: inline-block;
  margin: 0;
  padding: 0 15px;
}

.full-profile .info p {
  width: 100%;
  margin: 0;
  padding: 20px;
  display: inline-block;
  box-sizing: border-box;
}

.full-profile .info .close {
  position: absolute;
  background: transparent;
  border: none;
  color: #000;
  font-size: 28px;
  cursor: pointer;
  top: 10px;
  right: 10px;
}

.full-profile .gallery {
  width: calc(100% - 40px);
  flex-grow: 1;
  background-color: #cacaca;
  margin: 20px;
  margin-top: 0;
  padding: 10px;
  box-sizing: border-box;
  overflow-y: auto;
}

.full-profile .gallery img {
  width: calc(100% / 5 - 10px);
	margin: 5px;
	cursor: pointer;
}

.full-profile .gallery img.active {
  outline: solid 4px #ff5e00;
}
